import * as React from "react";
import {
    Button,
    HStack,
    Input,
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
    useDisclosure,
} from '@chakra-ui/react';

const PASSWORD_TO_FORM_MAP = {
    'test-01': 'https://forms.gle/u1rEn8RoKGuGWfcy8',
};

const RSVPButton = () => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const finalRef = React.useRef(null);
    const [value, setValue] = React.useState('');
    const handleChange = (event) => setValue(event.target.value)

    const openRSVP = () => {
        const password = localStorage.getItem('rsvp_password');

        if (!password) {
            onOpen();
        } else {
            const formUrl = PASSWORD_TO_FORM_MAP[password];
            window.open(formUrl, '_newtab');
        }
    };

    const enterPassword = () => {
        const trimmedValue = value.trim();
        const formUrl = PASSWORD_TO_FORM_MAP[trimmedValue];
        if (formUrl) {
            localStorage.setItem('rsvp_password', trimmedValue);
            window.open(formUrl, '_newtab');
            onClose();
        }
    };

    return (
        <>
            <HStack p={8} justifyContent="center" alignItems="center">
                <Button variant='outline' colorScheme='pink' size='lg' onClick={openRSVP}>RSVP</Button>
            </HStack>

            <Modal finalFocusRef={finalRef} isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>RSVP</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody>
                        <Input
                            value={value}
                            onChange={handleChange}
                            variant='flushed'
                            placeholder='Enter guest password'
                            size='sm'
                        />
                    </ModalBody>

                    <ModalFooter>
                        <Button variant='ghost' mr={3} onClick={onClose}>
                            Close
                        </Button>

                        <Button variant='ghost' onClick={enterPassword}>
                            Enter
                        </Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </>
    );
};

export default RSVPButton;